
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,i,u,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,fieldset,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;vertical-align:baseline;font:inherit;font-size:100%
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
audio:not([controls]){display:none;height:0
}
[hidden],template{display:none
}
script{display:none !important
}
html{font-family:sans-serif;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{margin:0;line-height:1
}
a,button,:focus,a:focus,button:focus,a:active,a:hover{outline:0
}
a{-webkit-user-drag:none;-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent
}
a[href]:hover{cursor:pointer
}
b,strong{font-weight:bold
}
dfn{font-style:italic
}
hr{box-sizing:content-box;height:0
}
code,kbd,pre,samp{font-size:1em;font-family:monospace, serif
}
pre{white-space:pre-wrap
}
q{quotes:"\201C" "\201D" "\2018" "\2019"
}
small{font-size:80%
}
sub,sup{position:relative;vertical-align:baseline;font-size:75%;line-height:0
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
fieldset{margin:0 2px;padding:0.35em 0.625em 0.75em;border:1px solid #c0c0c0
}
legend{padding:0;border:0
}
button,input,select,textarea{margin:0;font-size:100%;font-family:inherit;outline-offset:0;outline-style:none;outline-width:0;-webkit-font-smoothing:inherit;background-image:none
}
button,input{line-height:normal
}
button,select{text-transform:none
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{cursor:pointer;-webkit-appearance:button
}
button[disabled],html input[disabled]{cursor:default
}
input[type="search"]{box-sizing:content-box;-webkit-appearance:textfield
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
button::-moz-focus-inner,input::-moz-focus-inner{padding:0;border:0
}
textarea{overflow:auto;vertical-align:top
}
img{-webkit-user-drag:none
}
*{font-family:'SimHei'
}
table{border-spacing:0;border-collapse:collapse
}
textarea{resize:none
}
.flex-container{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:start;align-items:flex-start
}
.flex-1{-ms-flex:1;flex:1
}
.flex-2{-ms-flex:2;flex:2
}
.flex-3{-ms-flex:3;flex:3
}
.flex-4{-ms-flex:4;flex:4
}
.flex-5{-ms-flex:5;flex:5
}
.tcenter{text-align:center
}
.phone .load2{background-color:rgba(0,0,0,0.5);position:absolute;top:0px;left:0px;width:100%;height:100%;opacity:0.8
}
.phone .load2 .loader,.phone .load2 .loader:before,.phone .load2 .loader:after{border-radius:50%
}
.phone .load2 .loader:before,.phone .load2 .loader:after{position:absolute;content:''
}
.phone .load2 .loader:before{width:5.2em;height:10.2em;background:#808080;border-radius:10.2em 0 0 10.2em;top:-0.1em;left:-0.1em;transform-origin:5.2em 5.1em;animation:load2 2s infinite ease 1.5s
}
.phone .load2 .loader{font-size:11px;text-indent:-99999em;margin:13rem auto;position:relative;width:10em;height:10em;box-shadow:inset 0 0 0 1em #FFF
}
.phone .load2 .loader:after{width:5.2em;height:10.2em;background:#808080;border-radius:0 10.2em 10.2em 0;top:-0.1em;left:5.1em;transform-origin:0px 5.1em;animation:load2 2s infinite ease
}
@keyframes load2{
0%{transform:rotate(0deg)
}
100%{transform:rotate(360deg)
}
}
.phone .phoneinput{height:70px
}
.phone .phoneinput a{color:#000;text-decoration:none
}
.phone .phoneinput span{display:inline-block;border:none;width:84%;height:70px;line-height:70px;font-size:47px;color:#000;text-indent:16px
}
.phone .phoneinput .f12{font-size:39px
}
.phone .phoneinput i{background-image:url("../../static/cutter_50.png");width:23px;height:13px;background-size:23px 13px;display:inline-block;vertical-align:top;margin-top:27px
}
.phone .phone_icon{background-image:url("../../static/cutter_51.png");width:70px;height:70px;background-size:70px;display:inline-block;margin-top:3rem
}
.phone .flex-container .flex-1{height:500%;line-height:500%;text-align:center;border:1px #ccc solid;border-left:none;border-bottom:none
}
.phone .flex-container.last{border-bottom:1px #ccc solid
}
.phone .flex-container p{font-size:40px;color:#000
}



